
.message-container {
  height: 1em;
}
.message-container>.message {
  transition: all .3s ease;
  display: inline-block;
  width: 150px;
/*  position: absolute;*/  
  overflow: hidden;
  text-overflow: ellipsis;
}
.container {
  position: relative;
  z-index: 1;
  height: 100%;
}



